import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { TasksServiceProvider } from '../../providers/tasks-service/tasks-service';
import { TaskDetailsForm } from '../models/task-details-form';
import { TaskDetailsPage } from '../task-details/task-details';
import * as moment from 'moment/moment';


/**
 * Generated class for the TasksPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-tasks',
  templateUrl: 'tasks.html',
})
export class TasksPage {
  selectedItem: any;
  // public taskDetailsFormList: any;
  public taskDetailsFormList: any[] = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, public tasksServiceProvider: TasksServiceProvider) {
      tasksServiceProvider.load().subscribe(data => {
      // console.log(data);
      // console.log(data['tasksList']);
      this.taskDetailsFormList = data['tasksList'];

     
    })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TasksPage');
    let now = moment(); // add this 2 of 4
    console.log('hello world', now.format()); // add this 3 of 4
    console.log(now.add(7, 'days').format()); // add this 4of 4
  }

  getAllTasks(){
    
  }
  
  openTaskDetails(item) {
    // That's right, we're pushing to ourselves!
    this.navCtrl.push(TaskDetailsPage, {
      item: item
    });
  }

  // itemTapped(event, item) {
  //   // That's right, we're pushing to ourselves!
  //   this.navCtrl.push(TasksPage, {
  //     item: item
  //   });
  // }

}



