import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { TaskDetailsForm } from '../../pages/models/task-details-form';

import 'rxjs/add/operator/map';
import { Observable } from 'rxjs/Observable';
// import 'rxjs/add/operator/map';

/*
  Generated class for the TasksServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class TasksServiceProvider {

  // http://localhost:8080/MISDeskCRM/mapp/api/task/v2/getAll/userToken/4/0/pending.do
  public static readonly GET_ALL_DETAILS = environment.apiUrl + '/mapp/api/task/v2/getAll';
  public data: any;

  constructor(public http: HttpClient) {
    console.log('Hello TasksServiceProvider Provider');
  }

  // Load all github users
  load(): Observable < TaskDetailsForm[] > {

    const uid = 4;
    const cid = 0;
    const criteriaData = 'pending';
  
    const userToken = 'userToken'; // this.authService.getUserToken();
    const urlToCall = TasksServiceProvider.GET_ALL_DETAILS
    + '/' + userToken
    + '/' + uid
    + '/' + cid
    + '/' + criteriaData
    +  '.do';

    return this.http.get(urlToCall)
      .map(res => <any[]>res);
      
  }

  // getAllTasks(uid, cid, criteriaData) {
  //   return new Promise(resolve => {
  //     this.http.get(urlToCall)
  //       .map(res => res)
  //       .subscribe(data => {
  //         this.data = data;
  //         resolve(this.data);
  //       });
  //   });
  //   }

}



